<script>
import axios from "axios";

import Layout from "@/layouts/auth";
import {
  //authMethods,
  authFackMethods,
  notificationMethods,
} from "@/state/helpers";
import { mapState } from "vuex";

import appConfig from "@/app.config";
import { required } from "vuelidate/lib/validators";

/**
 * Login component
 */
export default {
  page: {
    title: "",
    meta: [
      {
        name: "description",
        content: appConfig.description,
      },
    ],
  },
  components: {
    Layout,
  },
  data() {
    return {
      userid: "admin",
      password: "123456",
      submitted: false,
      authError: null,
      tryingToLogIn: false,
      isAuthError: false,
    };
  },
  validations: {
    userid: {
      required
    },
    password: {
      required,
    },
  },
  computed: {
    ...mapState("authfack", ["status"]),
    notification() {
      return this.$store ? this.$store.state.notification : null;
    },
  },
  methods: {
    // ...authMethods,
    ...authFackMethods,
    ...notificationMethods,
    // Try to log the user in with the username
    // and password they provided.
    tryToLogIn() {
      this.submitted = true;
      // stop here if form is invalid
      this.$v.$touch();

      if (this.$v.$invalid) {
        return;
      } else {
        if (process.env.VUE_APP_DEFAULT_AUTH === "fakebackend") {
          const { userid, password } = this;
          if (userid && password) {
            this.login({
              userid,
              password,
            });
          }
        } else if (process.env.VUE_APP_DEFAULT_AUTH === "authapi") {
          axios
            .post("http://127.0.0.1:8000/api/login", {
              userid: this.userid,
              password: this.password,
            })
            .then((res) => {
              return res;
            });
        }
      }
    },
  },
  mounted() {},
};
</script>

<template>
  <Layout>
    <div class="account-pages mt-5 mb-5">
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-md-8 col-lg-6 col-xl-5">
            <div class="text-center mb-3">
              <a href="index.html" class="logo">
                <img
                  src="@/assets/images/logo-light.png"
                  alt=""
                  height="55"
                  class="logo-light mx-auto"
                />
              </a>
            </div>
            <div class="card">
              <div class="card-body p-4">
                <b-alert
                  v-model="isAuthError"
                  variant="danger"
                  class="mt-3"
                  dismissible
                  >{{ authError }}</b-alert
                >

                <div
                  v-if="notification.message"
                  :class="'alert ' + notification.type"
                >
                  {{ notification.message }}
                </div>
                <div class="text-center mb-4">
                  <h4 class="text-uppercase mt-0"></h4>
                </div>

                <form @submit.prevent="tryToLogIn">
                  <b-form-group
                    id="input-group-1"
                    label=""
                    label-for="input-1"
                    class="mb-3"
                  >
                    <b-form-input
                      id="input-1"
                      v-model="userid"
                      type="text"
                      placeholder=""
                      :class="{ 'is-invalid': submitted && $v.userid.$error }"
                    ></b-form-input>
                    <div
                      v-if="submitted && $v.userid.$error"
                      class="invalid-feedback"
                    >
                      <span v-if="!$v.userid.required">  .</span>
                    </div>
                  </b-form-group>


                  <b-form-group
                    id="input-group-2"
                    label=""
                    label-for="input-2"
                        class="mb-3"
                  >
                    <b-form-input
                      id="input-2"
                      v-model="password"
                      type="password"
                      placeholder=" "
                      :class="{ 'is-invalid': submitted && $v.password.$error }"
                    ></b-form-input>
                    <div
                      v-if="submitted && !$v.password.required"
                      class="invalid-feedback"
                    >
                       .
                    </div>
                  </b-form-group>

                  <div class="form-group mb-3">
                    <div class="custom-control custom-checkbox">
                      <input
                        type="checkbox"
                        class="custom-control-input"
                        id="checkbox-signin"
                        checked
                      />
                      <label class="custom-control-label" for="checkbox-signin"
                        ></label
                      >
                    </div>
                  </div>

                  <div class="form-group mb-0 text-center">
                    <button class="btn btn-primary btn-block" type="submit">
                      
                    </button>
                  </div>
                </form>
              </div>
              <!-- end card-body -->
            </div>
            <!-- end card -->

            <div class="row mt-3">
              <div class="col-12 text-center">
                <p class="text-muted">
                    
                <router-link
                  tag="a"
                  to="#"
                  class="text-dark ml-1"
                  ></router-link
                >
                </p>
              <p class="text-right">
                copyright             
              </p>
              </div>
              <!-- end col -->
            </div>
            <!-- end row -->
          </div>
          <!-- end col -->
        </div>
        <!-- end row -->
      </div>
      <!-- end container -->
    </div>
    <!-- end page -->
  </Layout>
</template>

<style></style>
